/* Piet Hello World - Clean and Simple */
.App {
  text-align: center;
  background: #000000;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-family: 'Courier New', monospace;
}

.piet-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.piet-title {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: bold;
  margin: 0;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.piet-grid {
  display: grid;
  grid-template-columns: repeat(20, 1fr);
  gap: 1px;
  background: #333333;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.2);
}

.piet-cell {
  width: 20px;
  height: 20px;
  border: none;
}

/* Piet Standard Colors */
.piet-cell.red { background-color: #FF0000; }
.piet-cell.green { background-color: #00FF00; }
.piet-cell.blue { background-color: #0000FF; }
.piet-cell.cyan { background-color: #00FFFF; }
.piet-cell.magenta { background-color: #FF00FF; }
.piet-cell.yellow { background-color: #FFFF00; }
.piet-cell.white { background-color: #FFFFFF; }
.piet-cell.black { background-color: #000000; }

/* Light Piet Colors */
.piet-cell.light-red { background-color: #FFC0C0; }
.piet-cell.light-green { background-color: #C0FFC0; }
.piet-cell.light-blue { background-color: #C0C0FF; }
.piet-cell.light-cyan { background-color: #C0FFFF; }
.piet-cell.light-magenta { background-color: #FFC0FF; }
.piet-cell.light-yellow { background-color: #FFFFC0; }

/* Dark Piet Colors */
.piet-cell.dark-red { background-color: #C00000; }
.piet-cell.dark-green { background-color: #00C000; }
.piet-cell.dark-blue { background-color: #0000C0; }
.piet-cell.dark-cyan { background-color: #00C0C0; }
.piet-cell.dark-magenta { background-color: #C000C0; }
.piet-cell.dark-yellow { background-color: #C0C000; }

.piet-info {
  color: #ffffff;
  text-align: center;
}

.piet-info p {
  margin: 5px 0;
  font-size: 1.1rem;
  opacity: 0.8;
}

/* Responsive Design */
@media (max-width: 768px) {
  .piet-grid {
    grid-template-columns: repeat(20, 1fr);
    gap: 0.5px;
    padding: 5px;
  }

  .piet-cell {
    width: 12px;
    height: 12px;
  }

  .piet-title {
    font-size: 1.8rem;
  }
}